ks-client-data-form {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-areas: 
        "type type"
        "contact contact"
        "name address"
        "agreements agreements"
        "submit submit";

    column-gap: 20px;
    row-gap: 20px;

    max-width: 900px;
}

ks-client-data-form[business] {
    grid-template-areas: 
        "type type"
        "contact business"
        "name address"
        "agreements agreements"
        "submit submit";
}

ks-client-data-form[password] {
    grid-template-areas: 
        "type type"
        "contact password"
        "name address"
        "agreements agreements"
        "submit submit";
}

ks-client-data-form[password][business] {
    grid-template-areas: 
        "type type" 
        "business business" 
        "contact password"
        "name address"
        "agreements agreements"
        "submit submit";
}

ks-client-data-form .type {
    grid-area: type;
    display: flex;
    margin-bottom: -15px;
}

ks-client-data-form[password] .type {
    margin-bottom: 0px;
}

ks-client-data-form[centered] .type {
    justify-content: center;
}

ks-client-data-form .business {
    text-align: center;
    grid-area: business;
}

ks-client-data-form .company .business {
    text-align: left;
}

ks-client-data-form .contact {
    grid-area: contact;
}

ks-client-data-form .password {
    grid-area: password;
}

ks-client-data-form .name {
    grid-area: name;
}

ks-client-data-form .address {
    grid-area: address;
}

ks-client-data-form .agreements {
    grid-area: agreements;
    font-size: 14px;
}

ks-client-data-form h1 {
    text-align: center;
    margin-bottom: 10px;
}

ks-client-data-form .type > * {
    margin-right: 10px;
}

ks-client-data-form .business {
    display: none;
}

ks-client-data-form[business] .business {
    display: block;
    animation: fade-in 0.4s ease;
}

ks-client-data-form ks-input-text, ks-client-data-form ks-input-select {
    margin-bottom: 10px;
}

ks-client-data-form .submit {
    grid-area: submit;
    width: 100%;
}

ks-client-data-form .submit > ks-icon {
    margin-left: 10px !important;
}

ks-client-data-form[password] .submit {
    margin-top: 20px;
}

ks-client-data-form[password] .submit input {
    background: rgb(24,141,219);
    background: linear-gradient(321deg, rgba(24,141,219,1) 0%, rgba(0,26,78,1) 100%);

    transition: opacity 0.3s ease;
}

ks-client-data-form[password] .submit input:hover {
    opacity: 0.9;
}

ks-client-data-form[password] .submit input:active {
    opacity: 0.8;
}


@media screen and (max-width: 640px) {
    ks-client-data-form {
        grid-template-columns: 1fr;
        grid-template-areas: 
            "contact"
            "type"
            "name"
            "address"
            "agreements"
            "submit";
    }

    ks-client-data-form[business] {
        grid-template-areas: 
            "contact"
            "type"
            "business"
            "name"
            "address"
            "agreements"
            "submit";
    }

    ks-client-data-form[password] {
        grid-template-areas: 
            "password"
            "type"
            "contact"
            "name"
            "address"
            "agreements"
            "submit";
    }

    ks-client-data-form[password][business] {
        grid-template-areas: 
            "password" 
            "type" 
            "business" 
            "contact"
            "name"
            "address"
            "agreements"
            "submit";
    }
}