import { Component, h, Prop, Element } from '@stencil/core';
import { account } from "../../../global/data/account";
import { register } from "../../../global/data/register";
import { marked } from 'marked';

@Component({
    tag: 'ks-client-data-form',
    styleUrl: 'clientDataForm.css'
})
export class ClientDataForm {
    @Element() root: HTMLElement;

    @Prop({reflect: true, mutable: true}) business: boolean = false;
    @Prop({reflect: true}) password: boolean = false;
    @Prop({reflect: true}) centered: boolean = false;

    onBusiness(status: boolean = false) {
        this.business = status;
    }

    componentWillLoad() {
        const values = account.get('clientDataForm');
        this.business = values?.business;
    }

	render() {
        const strings = account.get('strings') || register.get('strings');
        const countries = account.get('countries') || register.get('countries');
        const values = account.get('clientDataForm') || register.get('clientDataForm');
        
        const external = register.get('external');
        this.password = !external;

		return [
            <div class="type">
                <ks-input-check radio name="register_type"
                    label={strings.person} checked={ values?.person ?? true }
                    onChange={()=>this.onBusiness(false)}
                />

                <ks-input-check radio name="register_type"
                    label={strings.business} checked={ values?.business ?? false }
                    onChange={()=>this.onBusiness(true)}
                />
            </div>,

            <div class="business">
                <ks-input-text name="register_company" value={values?.company}
                    label={strings.company} icon={"briefcase"} right-icon
                />

                <ks-input-text name="register_taxid" value={values?.taxid}
                    label={strings.taxid} icon={"briefcase"} right-icon
                />
            </div>,

            <div class="contact">

                <ks-input-text email name="register_email" value={values?.email}
                    label={strings.email} icon={"at-sign"} right-icon
                />

                <ks-input-text name="register_phone" value={values?.phone}
                    label={strings.phone} icon={"phone"} right-icon
                />
            </div>,

            this.password ? <div class="card password">
                <ks-input-text password name="register_password" 
                    label={strings.password} icon={"key"} right-icon
                />

                <ks-input-text password name="register_repeat_password" 
                    label={strings.repeatPassword} icon={"key"} right-icon
                />
            </div> : null,

            <div class="name">
                <ks-input-text name="register_firstname" value={values?.firstname}
                    label={strings.firstname} icon={"user"} right-icon
                />

                <ks-input-text name="register_lastname" value={values?.lastname}
                    label={strings.lastname} icon={"user"} right-icon
                />

                <ks-input-select name="register_country"
                    label={strings.country}
                >
                    {countries.map(country =>
                        <option value={country.id} selected={country.id == values?.country}>
                            {country.name}
                        </option>    
                    )}
                </ks-input-select>
            </div>,

            <div class="address">
                <ks-input-text name="register_street" value={values?.street}
                    label={strings.street} icon={"map-pin"} right-icon
                />

                <ks-input-text name="register_city" value={values?.city}
                    label={strings.city} icon={"map-pin"} right-icon
                />

                <ks-input-text name="register_zip" value={values?.zip}
                    label={strings.zip} icon={"map-pin"} right-icon
                />
            </div>,

            <div class="agreements">
                <ks-input-check name="register_newsletter" checked={values?.newsletter}
                    label={marked.parseInline(strings.newsletter)}
                />
                <ks-input-check required name="register_agreement" checked={values?.agreement}
                    label={marked.parseInline(strings.agreement)}
                />
            </div>,

            <ks-button submit={!external} class="submit" name={strings.submit} round
                primary={this.password == true} icon={
                    external == "google" ? "google" :
                    external == "facebook" ? "facebook" :
                    ""
                }
            />
        ];
	}
}










